@import 'scss/vars.scss';

.situationsList {
  margin: 0 0.625rem;
  max-width: 63.5rem;
  overflow-x: scroll;
  padding: 7px 2px 6px;
  display: flex;
  align-items: center;
  font: 400 0.875rem var(--font-nunito);
  transition: all 1s;

	@media screen and (max-width: 1102px) {
		max-width: 58rem;
	}

  > li {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    background: #ffffff;
    border-radius: 2.5rem;
    flex-shrink: 0;
  }

  > li + li {
    margin-left: 0.5rem;
  }

  > li > button {
    padding: 0.7rem 1.875rem 0.46875rem;
    font: inherit;
		position: relative;

		&::after {
			content: attr(data-count);
			position: absolute;
			right: 2px;
			top: -7px;
			width: 1.25rem;
			height: 1.25rem;
			border-radius: .625rem;
			border: .5px solid black;
			background: var(--side-color);
			font: bold .875rem var(--font-comfortaa);
			line-height: 1.4;
			color: white;
		}

		&.hidden::after {
			visibility: hidden;
		}

    @media screen and (max-width: 1102px) {
			padding: 0.75rem 1.25rem 0.39rem;
    }
		
		@media screen and (max-height: 725px) {
			padding: 0.6rem 1.25rem 0.3rem;
		}
  }

  > li > button > span {
    display: inline-block;
    // padding-bottom: 0.1875rem;
    border-bottom: 2px solid transparent;
    transition: border-bottom var(--transition-speed);

		@media screen and (max-width: 1102px) {
			padding-bottom: 0.12rem;
    }

    &.active {
      border-bottom: 2px solid var(--side-color);
    }
  }
}

.situationsList::-webkit-scrollbar {
  height: 0;
}
